import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

const STATUS_ICON_WIDTH = 14;
const MENU_BUTTON_WIDTH = 44;

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    paddingLeft: STATUS_ICON_WIDTH + theme.spacing(2),
    paddingRight: ({ menu }) => (menu ? MENU_BUTTON_WIDTH : 0),
  },
}));

const DashboardHeader = ({ menu, children, ...rest }) => {
  const classes = useStyles({ menu });

  return (
    <Grid container spacing={2} classes={classes} {...rest}>
      {children}
    </Grid>
  );
};

DashboardHeader.propTypes = {
  menu: PropTypes.bool,
  children: PropTypes.node.isRequired,
};

DashboardHeader.defaultProps = {
  menu: false,
};

export default DashboardHeader;
