import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';

import StatusIndicator from './StatusIndicator';

const useRootStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    borderBottom: `${theme.palette.divider} 1px solid`,
    minHeight: '44px',
  },
}));

const useStatusStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(2),
  },
}));

const DashboardRow = ({ status, statusText, children, ...rest }) => {
  const rootClasses = useRootStyles();
  const statusClasses = useStatusStyles();

  return (
    <div className={rootClasses.root} {...rest}>
      {status && (
        <div className={statusClasses.root}>
          <StatusIndicator status={status} text={statusText} />
        </div>
      )}

      {children}
    </div>
  );
};

DashboardRow.propTypes = {
  status: PropTypes.oneOf(['info', 'success', 'warning', 'error', 'deny']),
  statusText: PropTypes.string,
  children: PropTypes.node.isRequired,
};

DashboardRow.defaultProps = {
  status: 'info',
  statusText: null,
};

export default DashboardRow;
