import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Collapse from '@material-ui/core/Collapse';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';

const useButtonStyles = makeStyles(theme => ({
  root: {
    padding: `${theme.spacing(1.25)}px ${theme.spacing(1)}px`,
    justifyContent: 'flex-start',
    width: '100%',
    // using a 16px font here had a pretty jarring bump in size when switching to the active
    // font weight (heavy weight was a smaller font). at least on chrome. 17px looked much better.
    fontSize: `${17 / 16}rem`,
    color: ({ active }) =>
      active ? theme.palette.text.blue : theme.palette.text.primary,
    fontWeight: ({ active }) =>
      active
        ? theme.typography.fontWeightHeavy
        : theme.typography.fontWeightMedium,
  },
  label: {
    display: 'flex',
    alignItems: 'flex-start',
  },
}));

const NavItem = ({
  icon,
  text,
  href,
  linkProps,
  active,
  highlight,
  submenuRef,
  children,
  ...rest
}) => {
  const theme = useTheme();
  const buttonClasses = useButtonStyles({ active });

  const [expanded, setExpanded] = useState(false);

  useEffect(() => {
    submenuRef({
      href,
      expand: () => setExpanded(true),
      collapse: () => setExpanded(false),
      toggle: () => setExpanded(!!expanded),
    });
  }, []);

  return (
    <ListItem
      disableGutters
      key={href}
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        paddingTop: 0,
        paddingBottom: 0,
      }}
      {...rest}
    >
      <Box display="flex" width="100%">
        <Button
          test-ref="NavItem-button"
          underline="none"
          href={href}
          {...linkProps}
          classes={buttonClasses}
        >
          {icon && (
            <Box
              test-ref="NavItem-icon"
              display="flex"
              color={theme.palette.icon}
              witdth={24}
              height={24}
              alignItems="center"
              mt={0.25}
              mr={3}
            >
              <Badge invisible={!highlight} color="primary" variant="dot">
                {icon}
              </Badge>
            </Box>
          )}
          <Badge invisible={!!icon || !highlight} color="primary" variant="dot">
            {text}
          </Badge>
        </Button>

        {children && !expanded && (
          <IconButton onClick={() => setExpanded(true)}>
            <ExpandMoreIcon />
          </IconButton>
        )}

        {children && expanded && (
          <IconButton onClick={() => setExpanded(false)}>
            <ExpandLessIcon />
          </IconButton>
        )}
      </Box>

      <Collapse
        in={children && expanded}
        style={{
          width: '100%',
        }}
      >
        <Box ml={7}>
          <List
            style={{
              padding: 0,
            }}
          >
            {children}
          </List>
        </Box>
      </Collapse>
    </ListItem>
  );
};

NavItem.propTypes = {
  icon: PropTypes.node,
  text: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  active: PropTypes.bool,
  linkProps: PropTypes.object,
  highlight: PropTypes.bool,
  submenuRef: PropTypes.func,
  children: PropTypes.node,
};

NavItem.defaultProps = {
  icon: null,
  active: false,
  linkProps: {
    component: 'a',
  },
  highlight: false,
  submenuRef: Function.prototype,
  children: null,
};

export default NavItem;
