import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import ButtonBase from '@material-ui/core/ButtonBase';
import Skeleton from '@material-ui/lab/Skeleton';

import Paragraph from './Paragraph';

const useRootStyles = makeStyles(theme => ({
  root: {
    background: theme.palette.background.default,
    marginBottom: theme.spacing(2),
  },
}));

const useHeadStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
}));

const useContentStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
}));

const DashboardItem = ({
  button,
  statusColor,
  statusIcon,
  statusText,
  primaryText,
  secondaryText,
  rightColumn,
  renderHead,
  renderChildren,
  children,
  ...rest
}) => {
  const theme = useTheme();
  const rootClasses = useRootStyles();
  const headClasses = useHeadStyles();
  const contentClasses = useContentStyles();

  const head = (
    <Paper classes={headClasses}>
      <Box display="flex">
        <Box flex={1}>
          <Box display="flex">
            {statusIcon ? (
              <Box
                test-ref="DashboardItem-status"
                component="span"
                display="inline-flex"
                alignItems="center"
                color={statusColor
                  .split('.')
                  .reduce((obj, key) => obj[key], theme.palette)}
                mr={1.5}
                mb={1}
              >
                {statusText ? (
                  <Tooltip title={statusText}>{statusIcon}</Tooltip>
                ) : (
                  statusIcon
                )}
              </Box>
            ) : (
              <Box mb={1} height={24} />
            )}
            <Box mr={2}>
              <Paragraph test-ref="DashboardItem-primary-text" mb={0}>
                {primaryText}
              </Paragraph>
            </Box>
          </Box>
          <Box test-ref="DashboardItem-secondary-text" color="text.hint">
            {secondaryText}
          </Box>
        </Box>
        <Box display="flex">
          <Box>{rightColumn}</Box>
        </Box>
      </Box>
    </Paper>
  );

  return (
    <div classes={rootClasses} {...rest}>
      {button ? (
        <ButtonBase
          component="div"
          style={{
            display: 'block',
            width: '100%',
            textAlign: 'left',
          }}
        >
          {renderHead(head)}
        </ButtonBase>
      ) : (
        renderHead(head)
      )}

      {children && (
        <div className={contentClasses.root}>{renderChildren(children)}</div>
      )}
    </div>
  );
};

DashboardItem.Skeleton = ({ showIcon }) => (
  <DashboardItem
    statusIcon={
      showIcon ? (
        <Skeleton
          component="span"
          variant="circle"
          width={24}
          height={24}
          style={{ margin: 0 }}
        />
      ) : null
    }
    primaryText={
      <Skeleton
        component="span"
        width={300}
        height={24}
        style={{ margin: 0 }}
      />
    }
    secondaryText={
      <Skeleton
        component="span"
        height={20}
        width={120}
        style={{ margin: 0 }}
      />
    }
  />
);

DashboardItem.Skeleton.propTypes = {
  showIcon: PropTypes.bool,
};

DashboardItem.Skeleton.defaultProps = {
  showIcon: true,
};

DashboardItem.propTypes = {
  button: PropTypes.bool,
  statusColor: PropTypes.string,
  statusIcon: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  statusText: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  primaryText: PropTypes.oneOfType([PropTypes.string, PropTypes.node])
    .isRequired,
  secondaryText: PropTypes.oneOfType([PropTypes.string, PropTypes.node])
    .isRequired,
  rightColumn: PropTypes.node,
  renderHead: PropTypes.func,
  renderChildren: PropTypes.func,
  children: PropTypes.node,
};

DashboardItem.defaultProps = {
  button: false,
  statusColor: 'text.primary',
  statusIcon: null,
  statusText: null,
  rightColumn: null,
  renderHead: head => head,
  renderChildren: children => children,
  children: null,
};

export default DashboardItem;
