import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';

const useButtonStyles = makeStyles(theme => ({
  root: {
    display: 'block',
    padding: `${theme.spacing(1.25)}px ${theme.spacing(1)}px`,
    justifyContent: 'flex-start',
    width: '100%',
    fontSize: `${14 / 16}rem`,
    textTransform: 'none',
    color: ({ active }) =>
      active ? theme.palette.text.blue : theme.palette.text.primary,
    fontWeight: theme.typography.fontWeightMedium,
  },
}));

const NavSubItem = ({ text, href, linkProps, active, ...rest }) => {
  const buttonClasses = useButtonStyles({ active });

  return (
    <ListItem
      disableGutters
      key={href}
      style={{
        display: 'flex',
        alignItems: 'flex-start',
        paddingTop: 0,
        paddingBottom: 0,
      }}
      {...rest}
    >
      <Button
        test-ref="NavSubItem-button"
        underline="none"
        href={href}
        classes={buttonClasses}
        {...linkProps}
      >
        {text}
      </Button>
    </ListItem>
  );
};

NavSubItem.propTypes = {
  text: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
  href: PropTypes.string.isRequired,
  active: PropTypes.bool,
  linkProps: PropTypes.object,
};

NavSubItem.defaultProps = {
  active: false,
  linkProps: {
    component: 'a',
  },
};

export default NavSubItem;
