import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';

const useRootStyles = makeStyles(theme => ({
  root: {
    padding: `${theme.spacing(1.25)}px ${theme.spacing(4)}px`,
    justifyContent: 'flex-start',
    borderRadius: 0,
    minHeight: '64px',
    // using a 16px font here had a pretty jarring bump in size when switching to the active
    // font weight (heavy weight was a smaller font). at least on chrome. 17px looked much better.
    fontSize: `${17 / 16}rem`,
    fontWeight: ({ active }) =>
      active
        ? theme.typography.fontWeightHeavy
        : theme.typography.fontWeightMedium,
    backgroundColor: ({ active }) =>
      active ? theme.palette.background.secondary : theme.palette.white.main,
  },
  label: {
    display: 'flex',
    alignItems: 'center',
  },
}));

const useIconStyles = makeStyles(() => ({
  root: {
    '& svg': {
      width: '32px',
      height: '32px',
    },
  },
}));

const NavTab = ({ icon, text, href, linkProps, active, ...rest }) => {
  const theme = useTheme();
  const rootClasses = useRootStyles({ active });
  const iconClasses = useIconStyles();

  return (
    <Button
      test-ref="NavTab-button"
      underline="none"
      href={href}
      classes={rootClasses}
      {...linkProps}
      {...rest}
    >
      {active && icon && (
        <Box
          test-ref="NavTab-icon"
          display="flex"
          color={theme.palette.icon}
          witdth={32}
          height={32}
          alignItems="center"
          mt={0.25}
          mr={2}
          classes={iconClasses}
        >
          {icon}
        </Box>
      )}
      {text}
    </Button>
  );
};

NavTab.propTypes = {
  icon: PropTypes.node,
  text: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  active: PropTypes.bool,
  linkProps: PropTypes.object,
};

NavTab.defaultProps = {
  icon: null,
  active: false,
  linkProps: {
    component: 'a',
  },
};

export default NavTab;
