import React from 'react';
import PropTypes from 'prop-types';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import ButtonBase from '@material-ui/core/ButtonBase';

import Paragraph from './Paragraph';

const Tile = ({ button, onClick, p, children, ...rest }) => {
  const inner = (
    <Paper
      test-ref="Tile-paper"
      style={{
        borderRadius: '26px',
      }}
      {...rest}
    >
      <Box p={p}>
        <Paragraph component="div" mb={0}>
          {children}
        </Paragraph>
      </Box>
    </Paper>
  );

  if (button) {
    return (
      <ButtonBase
        test-ref="Tile-button-base"
        onClick={onClick}
        style={{
          display: 'block',
          width: '100%',
          borderRadius: '26px',
        }}
      >
        {inner}
      </ButtonBase>
    );
  }

  return inner;
};

Tile.propTypes = {
  button: PropTypes.bool,
  onClick: PropTypes.func,
  p: PropTypes.number,
  children: PropTypes.node.isRequired,
};

Tile.defaultProps = {
  button: false,
  p: 3,
  onClick: Function.prototype,
};

export default Tile;
