import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';

import Tile from './Tile';

const useStyles = makeStyles(theme => ({
  root: {
    color: theme.palette.secondary.main,
    fontFamily: theme.typography.button.fontFamily,
    fontWeight: theme.typography.button.fontWeight,
    lineHeight: theme.typography.button.lineHeight,
    letterSpacing: theme.typography.button.letterSpacing,
    textTransform: theme.typography.button.textTransform,
    textAlign: 'left',
    fontSize: `${16 / 16}rem`,
  },
}));

const ContentLink = React.forwardRef((props, ref) => {
  const { tile, icon, children, ...rest } = props;

  const classes = useStyles();

  const inner = (
    <Box display="flex" justifyContent="left" classes={classes}>
      <Box mt={0.25} mr={3}>
        {icon}
      </Box>
      <Box>{children}</Box>
    </Box>
  );

  if (tile) {
    return (
      <Link ref={ref} underline="none" {...rest}>
        <Tile button>{inner}</Tile>
      </Link>
    );
  }

  return (
    <Link ref={ref} underline="none" {...rest}>
      {inner}
    </Link>
  );
});

ContentLink.propTypes = {
  tile: PropTypes.bool,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
};

ContentLink.defaultProps = {
  tile: false,
};

export default ContentLink;
