import React, { useState, useContext, useEffect } from 'react';
import PropTypes from 'prop-types';
import refractor from 'refractor/core';
import toH from 'hast-to-hyperscript';
import copy from 'clipboard-copy';
import { Subject, of, from } from 'rxjs';
import { switchMap, tap, delay } from 'rxjs/operators';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';

import prismCss from '../styles/prism-dracula.css';
import ContentCopyIcon from '../icons/ContentCopy';
import bash from '../languages/bash';

const useCodeStyles = makeStyles(theme => ({
  root: {
    fontFamily: 'Cousine, monospace, monospace',
    fontSize: theme.typography.body1.fontSize,
    fontWeight: theme.typography.body1.fontWeight,
  },
}));

const useCopyIconStyles = makeStyles(theme => ({
  root: {
    '& path': {
      fill: theme.palette.white.main,
    },
  },
}));

const GrammarContext = React.createContext([]);

const GrammarProvider = ({ grammars, children }) => {
  const contextGrammars = useContext(GrammarContext);

  refractor.register(bash);
  grammars.forEach(grammar => refractor.register(grammar));

  return (
    <GrammarContext.Provider value={contextGrammars.concat(grammars)}>
      {children}
    </GrammarContext.Provider>
  );
};

GrammarProvider.propTypes = {
  grammars: PropTypes.array.isRequired,
  children: PropTypes.node.isRequired,
};

const CodeBlock = ({
  language,
  copyText,
  autoCopy,
  onCopyClick,
  children,
  ...rest
}) => {
  const defaultTooltip = 'Copy to clipboard';
  const [isHovered, setIsHovered] = useState(false);
  const [tooltip, setTooltip] = useState(defaultTooltip);
  const copyClick$ = useState(new Subject())[0];
  const grammars = useContext(GrammarContext);

  const codeClasses = useCodeStyles();
  const copyIconClasses = useCopyIconStyles();

  let parsedCopyText = copyText;

  if (!parsedCopyText && autoCopy) {
    if (typeof children === 'string') {
      parsedCopyText = children.trim();
    } else {
      // eslint-disable-next-line no-console
      console.log(
        "Can't enable auto-copy for code block with non-string children. Try using backticks in JSX {` ... `}."
      );
    }
  }

  // listen for copy button clicks and show "Copied!" text
  useEffect(() => {
    const sub = copyClick$
      .pipe(
        switchMap(() =>
          from(copy(parsedCopyText)).pipe(
            tap(() => setTooltip('Copied!')),
            tap(onCopyClick({ language, copyText: parsedCopyText })),
            delay(3000)
          )
        )
      )
      .subscribe(() => {
        setTooltip(defaultTooltip);
      });

    return () => sub.unsubscribe();
  }, [parsedCopyText]);

  const theme = useTheme();
  const showCopy = parsedCopyText && isHovered;

  let code = children;

  if (
    language &&
    (refractor.registered(language) ||
      grammars.map(grammar => grammar.displayName).includes(language))
  ) {
    const nodes = refractor.highlight(code, language);
    code = toH(React.createElement, { type: 'root', children: nodes });
  }

  return (
    <Box
      test-ref="CodeBlock-code-box"
      position="relative"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* eslint-disable react/no-danger */}
      <style type="text/css" dangerouslySetInnerHTML={{ __html: prismCss }} />

      <Box
        test-ref="CodeBlock-copy-box"
        visibility={showCopy ? 'visible' : 'hidden'}
        position="absolute"
        top={theme.spacing(0.2)}
        right={theme.spacing(1)}
      >
        <Tooltip
          test-ref="CodeBlock-tooltip"
          title={tooltip}
          placement="top"
          onClose={() =>
            of(defaultTooltip)
              .pipe(delay(500))
              .subscribe(msg => setTooltip(msg))
          }
          style={{ textAlign: 'center' }}
        >
          <IconButton
            test-ref="CodeBlock-copy-button"
            onClick={() => copyClick$.next()}
          >
            <ContentCopyIcon classes={copyIconClasses} />
          </IconButton>
        </Tooltip>
      </Box>

      <pre className={`language-${language}`} {...rest}>
        <code className={`language-${language} ${codeClasses.root}`}>
          {code}
        </code>
      </pre>
    </Box>
  );
};

CodeBlock.propTypes = {
  language: PropTypes.string,
  copyText: PropTypes.string,
  autoCopy: PropTypes.bool,
  onCopyClick: PropTypes.func,
  children: PropTypes.string.isRequired,
};

CodeBlock.defaultProps = {
  language: undefined,
  copyText: undefined,
  autoCopy: false,
  onCopyClick: Function.prototype,
};

CodeBlock.GrammarProvider = GrammarProvider;

export default CodeBlock;
