import React from 'react';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import Link from '@material-ui/core/Link';

import Moment from './Moment';

const DashboardColumn = ({
  chip,
  moment,
  text,
  linkComponent,
  href,
  linkProps,
  momentProps,
  ...rest
}) => {
  let content = text;

  if (href) {
    const LinkComponent = linkComponent;

    content = (
      <LinkComponent
        color="inherit"
        underline="hover"
        href={href}
        {...linkProps}
      >
        {content}
      </LinkComponent>
    );
  }

  if (moment) {
    content = (
      <Moment fromNow tooltip {...momentProps}>
        {content}
      </Moment>
    );
  }

  if (chip) {
    content = (
      <Chip
        clickable
        label={text}
        component={linkComponent}
        href={href}
        {...linkProps}
      />
    );
  }

  return (
    <Grid item {...rest}>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          whiteSpace: 'nowrap',
          overflow: 'hidden',
        }}
      >
        {content}
      </div>
    </Grid>
  );
};

DashboardColumn.propTypes = {
  chip: PropTypes.bool,
  moment: PropTypes.bool,
  text: PropTypes.string.isRequired,
  href: PropTypes.string,
  linkComponent: PropTypes.object,
  linkProps: PropTypes.object,
  momentProps: PropTypes.object,
};

DashboardColumn.defaultProps = {
  chip: false,
  moment: false,
  href: null,
  linkComponent: Link,
  linkProps: {},
  momentProps: {},
};

export default DashboardColumn;
