import React from 'react';
import PropTypes from 'prop-types';
import ReactMoment from 'react-moment';
import { makeStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles(theme => ({
  root: ({ tooltip }) => ({
    ...(tooltip
      ? {
          cursor: 'default',
          borderBottom: `1px dotted ${theme.palette.text.primary}`,
        }
      : {}),
  }),
}));

const Moment = ({
  tooltip,
  tooltipDateFormat,
  tooltipTimeFormat,
  children,
  ...rest
}) => {
  const classes = useStyles({ tooltip });

  return (
    <span className={classes.root}>
      {tooltip ? (
        <Tooltip
          title={
            <>
              <ReactMoment format={tooltipDateFormat}>{children}</ReactMoment>{' '}
              at{' '}
              <ReactMoment format={tooltipTimeFormat}>{children}</ReactMoment>
            </>
          }
        >
          <ReactMoment {...rest}>{children}</ReactMoment>
        </Tooltip>
      ) : (
        <ReactMoment {...rest}>{children}</ReactMoment>
      )}
    </span>
  );
};

Moment.propTypes = {
  tooltip: PropTypes.bool,
  tooltipDateFormat: PropTypes.string,
  tooltipTimeFormat: PropTypes.string,
  children: PropTypes.oneOfType([PropTypes.node, PropTypes.object]),
};

Moment.defaultProps = {
  tooltip: false,
  tooltipDateFormat: 'ddd, MMM D, YYYY',
  tooltipTimeFormat: 'h:mma',
  children: null,
};

export default Moment;
